body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header-container {
    display: flex;
    justify-content: space-between;
    background-color: aquamarine;
    padding-top: 10px;
    padding-bottom:10px;
}
#header-title {
    margin-left: 15px
}



#registration-button {
    margin-top: 15px
}
#authorization-button {
    margin-top: 15px
}
#task-container {
    min-height: 150px;
    width: 100%;
    margin:20px auto;
    border: 1px solid black;
    padding: 10px 10px;
    box-shadow: 5px 5px 10px rgba(0,0,0,0.5)
}
#task-container:hover {
    box-shadow: 10px 10px 15px rgba(0,0,0,0.5)
}



#task-checked {
    margin-left: 500px;
    color: green
}

#task-title {
    margin-top: 15px;
    display:inline-block
}


#task-buttons {
    margin-bottom:5px;
    display: flex;
    align-items: baseline

}


#task-button-edit {
    margin-left: 300px
}
#task-priority {
    color: blue
}


#createtaskform-container {
    padding: 15px
}


#menu-container {
    margin-top:15px;
    display: flex;
    justify-content: space-around

}
.tasks-container {
  width: 600px;
  margin: 0px auto;

}
